<template>
    <v-container class="py-10" fluid>
        <v-row class="justify-center">
            <v-col cols="12" md="10">
                <v-alert
                    v-model="alert"
                    :type="alertType"
                    border="left"
                    close-text="Close Alert"
                    elevation="10"
                    dismissible
                    transition="scale-transition"
                    style="
                        position: fixed;
                        top: 20%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                        z-index: 9999;
                        max-width: 35rem;
                    "
                >
                    {{ alertMessage }}
                </v-alert>
                <v-card class="rounded-lg pb-2">
                    <v-toolbar color="green" dark>
                        <v-toolbar-title>Terms and Conditions</v-toolbar-title>
                        <v-spacer />
                        <v-btn
                            color="green"
                            class="white--text ml-2"
                            fab
                            small
                            rounded
                            @click="addDialog = true"
                        >
                            <v-icon> mdi-plus </v-icon>
                        </v-btn>
                    </v-toolbar>
                    <v-dialog
                        v-model="addDialog"
                        :fullscreen="this.$vuetify.breakpoint.smAndDown"
                        max-width="800"
                    >
                        <v-card class="rounded-lg pb-2">
                            <v-toolbar color="green" dark>
                                <v-toolbar-title>
                                    Create A New Template
                                </v-toolbar-title>
                            </v-toolbar>
                            <v-container>
                                <v-row>
                                    <v-col>
                                        <p>Template Name:</p>
                                        <v-text-field v-model="addItem.name" />
                                    </v-col>
                                </v-row>
                                <v-row>
                                    <v-col>
                                        <p>Template Alias:</p>
                                        <v-text-field v-model="addItem.alias" />
                                    </v-col>
                                </v-row>

                                <v-row>
                                    <v-col>
                                        <p>Template Content:</p>
                                        <v-text-field
                                            v-model="addItem.content"
                                        />
                                    </v-col>
                                </v-row>
                                <v-row>
                                    <v-btn
                                        class="ma-2"
                                        color="error"
                                        @click="closeDialog('add')"
                                        >Cancel</v-btn
                                    >
                                    <v-btn
                                        class="ma-2"
                                        color="success"
                                        @click="createTerms"
                                        >Add</v-btn
                                    >
                                </v-row>
                            </v-container>
                        </v-card>
                    </v-dialog>
                    <v-dialog
                        v-model="editDialog"
                        :fullscreen="this.$vuetify.breakpoint.smAndDown"
                        max-width="800"
                    >
                        <v-card class="rounded-lg pb-2">
                            <v-toolbar color="green" dark>
                                <v-toolbar-title>
                                    Update Template
                                </v-toolbar-title>
                            </v-toolbar>
                            <v-container>
                                <v-row>
                                    <v-col>
                                        <p>Template Name:</p>
                                        <v-text-field v-model="editItem.name" />
                                    </v-col>
                                </v-row>
                                <v-row>
                                    <v-col>
                                        <p>Template Alias:</p>
                                        <v-text-field
                                            v-model="editItem.alias"
                                        />
                                    </v-col>
                                </v-row>

                                <v-row>
                                    <v-col>
                                        <p>Template Content:</p>
                                        <v-text-field
                                            v-model="editItem.content"
                                        />
                                    </v-col>
                                </v-row>
                                <v-row>
                                    <v-btn
                                        class="ma-2"
                                        color="error"
                                        @click="closeDialog('edit')"
                                        >Cancel</v-btn
                                    >
                                    <v-btn
                                        class="ma-2"
                                        color="success"
                                        @click="updateTerms"
                                        >Update</v-btn
                                    >
                                </v-row>
                            </v-container>
                        </v-card>
                    </v-dialog>

                    <template>
                        <v-data-table
                            :headers="headers"
                            :items="results"
                            :items-per-page="5"
                            :loading="dataLoading"
                            loading-text="Loading... Please wait"
                        >
                            <template v-slot:[`item.actions`]="{ item }">
                                <v-menu
                                    transition="scroll-y-transition"
                                    bottom
                                    left
                                >
                                    <template v-slot:activator="{ on, attrs }">
                                        <v-btn icon v-bind="attrs" v-on="on">
                                            <v-icon> mdi-dots-vertical </v-icon>
                                        </v-btn>
                                    </template>
                                    <v-list>
                                        <v-list-item>
                                            <v-btn
                                                color="warning"
                                                text
                                                @click="openEditDialog(item)"
                                            >
                                                <v-icon small left>
                                                    mdi-pencil
                                                </v-icon>
                                                Edit
                                            </v-btn>
                                        </v-list-item>

                                        <v-list-item>
                                            <v-btn
                                                color="error"
                                                text
                                                @click="deleteTerms(item)"
                                            >
                                                <v-icon small left>
                                                    mdi-delete
                                                </v-icon>
                                                Delete
                                            </v-btn>
                                        </v-list-item>
                                    </v-list>
                                </v-menu>
                            </template>
                            <template v-slot:no-data>
                                <v-alert
                                    prominent
                                    text
                                    type="info"
                                    class="ma-2"
                                >
                                    <v-row align="center">
                                        <v-col class="grow">
                                            No records found
                                        </v-col>
                                        <v-col class="shrink">
                                            <v-btn
                                                color="green"
                                                class="my-2 white--text"
                                                @click="initialize"
                                            >
                                                Retry
                                            </v-btn>
                                        </v-col>
                                    </v-row>
                                </v-alert>
                            </template>
                        </v-data-table>
                    </template>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>
<script>
import axios from 'axios';
import { mapGetters } from 'vuex';

export default {
    name: 'TermsAndConditionsManage',
    data() {
        return {
            alert: false,
            alertMessage: '',
            alertType: 'success',
            dataLoading: false,
            addDialog: false,
            editDialog: false,
            results: [],
            addItem: {
                alias: '',
                name: '',
                content: '',
            },
            editItem: {
                id: '',
                alias: '',
                name: '',
                content: '',
            },
            headers: [
                {
                    text: 'Template Name',
                    align: 'start',
                    sortable: true,
                    value: 'name',
                },
                {
                    text: 'Template Alias',
                    value: 'alias',
                },
                {
                    text: 'Template Content',
                    value: 'content',
                },
                { text: 'Actions', value: 'actions', sortable: false },
            ],
        };
    },
    computed: {
        ...mapGetters(['getInternalCoreClearController']),
    },
    mounted() {
        this.initialize();
    },
    methods: {
        initialize() {
            this.getTerms();
            this.getTermsLog();
        },
        async getTerms() {
            this.dataLoading = true;
            try {
                let response = await axios.get(
                    `${this.getInternalCoreClearController}/terms_templates`,
                    {
                        withCredentials: true,
                        crossDomain: true,
                    },
                );
                if (response.data.success === true) {
                    // console.log(response);
                    this.results = response.data.results;
                    
                } else {
                    console.log('A problem occured');
                }
                this.dataLoading = false;
            } catch (error) {
                console.error('Could not get terms');
                console.error(error);
            }
        },
        async getTermsLog() {
            this.dataLoading = true;
            try {
                let response = await axios.get(
                    `${this.getInternalCoreClearController}/terms_log`,
                    {
                        withCredentials: true,
                        crossDomain: true,
                    },
                );
                if (response.data.success === true) {
                    console.log(response);
                    this.dataLoading = false;
                } else {
                    console.log('A problem occured');
                }
            } catch (error) {
                console.error('Could not get log');
                console.error(error);
            }
        },
        async getTermsById() {
            try {
                let response = await axios.get(
                    `${this.getInternalCoreClearController}/terms_templates`,
                    {
                        withCredentials: true,
                        crossDomain: true,
                        params: { template_id: 'templateId' },
                    },
                );
                if (response) {
                    console.log(response);
                }
            } catch (error) {
                console.error('Could not get terms');
                console.error(error);
            }
        },
        async createTerms() {
            console.log('hello world');
            let formData = new FormData();
            formData.append('alias', this.addItem.alias);
            formData.append('name', this.addItem.name);
            formData.append('content', this.addItem.content);
            try {
                let response = await axios.post(
                    `${this.getInternalCoreClearController}/update_terms_template`,
                    formData,
                    {
                        withCredentials: true,
                        crossDomain: true,
                    },
                );
                if (response.data.success === true) {
                    console.log(response);
                    this.alert = true;
                    this.alertMessage = 'Terms Created';
                    this.alertType = 'success';
                    setTimeout(() => {
                        location.reload();
                    }, 1500);
                } else {
                    console.log('A problem occured');
                }
            } catch (error) {
                console.error('Could not create terms');
                console.error(error);
            }
        },
        async updateTerms() {
            console.log(this.editItem);
            let formData = new FormData();
            formData.append('template_id', this.editItem.id);
            formData.append('alias', this.editItem.alias);
            formData.append('name', this.editItem.name);
            formData.append('content', this.editItem.content);
            try {
                let response = await axios.post(
                    `${this.getInternalCoreClearController}/update_terms_template`,
                    formData,
                    {
                        withCredentials: true,
                        crossDomain: true,
                    },
                );
                if (response.data.success === true) {
                    console.log(response);
                    this.alert = true;
                    this.alertMessage = 'Terms Updated';
                    this.alertType = 'success';
                    setTimeout(() => {
                        location.reload();
                    }, 1500);
                } else {
                    console.log('A problem occured');
                }
            } catch (error) {
                console.error('Could not update terms');
                console.error(error);
            }
        },
        async deleteTerms(item) {
            let formData = new FormData();
            formData.append('template_id', item.id);
            try {
                let response = await axios.post(
                    `${this.getInternalCoreClearController}/delete_terms_template`,
                    formData,
                    {
                        withCredentials: true,
                        crossDomain: true,
                    },
                );
                if (response.data.success === true) {
                    console.log(response);
                    this.alert = true;
                    this.alertMessage = 'Terms Deleted';
                    this.alertType = 'success';
                    setTimeout(() => {
                        location.reload();
                    }, 1500);
                } else {
                    console.log('A problem occured');
                }
            } catch (error) {
                console.error('Could not delete terms');
                console.error(error);
            }
        },
        openEditDialog(item) {
            this.editItem = item;
            this.editDialog = true;
        },
        closeDialog(item) {
            if (item == 'add') {
                this.addDialog = false;
                this.addItem = {
                    alias: '',
                    name: '',
                    content: '',
                };
            } else if (item == 'edit') {
                this.editDialog = false;
                this.editItem = {
                    id: '',
                    alias: '',
                    name: '',
                    content: '',
                };
            }
        },
    },
};
</script>
